@import "variables";
@import 'fonts';
@import 'clearDefaultStyle';



button {
  @extend %b;
  line-height: 1;
  padding-bottom: 6px;
  transition: 0.25s ease;
}

.button {
  :hover {
    @extend %bhover;
  }
}

body {
  font-family: 'Montserrat';
  font-size: 16px;
  color: $gray;
}

body:scrollbar-track {
  background-color: #f5f5f5;
}

body:scrollbar {
  width: 8px;
  background-color: #95e1d3;
}

body:scrollbar-thumb {
  border-radius: 8px;
  background-color: #95e1d3;
}


h1 {
  @extend %gTitle;
  font-size: 60px;
}
h2 {
  @extend %title;
}
.newsTitle {
  font-size: 40px;
  color: $gray;
}
.newsText {
  font-size: 16px;
  line-height: 1.6;
}
.sectionTitle {
  @extend %goldTitle;
  grid-area: sectionTitle1;
}
//for decorate h3 text
h3 {
  font-family: Teko;
  font-size: 30px;
  color: $gray;
  letter-spacing: 0.06;
  text-transform: uppercase;
}
//for link of more item
h4 {
  font: italic 14px 'Montserrat';
}

.container {
  width: 1200px;
  margin: 0 auto;
}






@import 'headerUp';
@import 'navigationMenu';
@import 'firstScreen';
@import 'products';
@import 'news';

@import 'footer';

// Bussines start
.bussinesSuptitle {
  grid-area: viewMore2;
  margin-left: 20px;
  font-style: italic;
  font-size: 20px;
}

.bussinesItem {
  margin-bottom: 40px;
  display: flex;
  align-items: center;
}

.bussinesItem_Img {
  position: relative;
  img {
    position: relative;
    z-index: 1;
  }
}
.bussinesItem_Img_frame {
  position: absolute;
  top: -13px;
  left: 13px;
  width: 98.5%;
  height: 97.5%;
  border: 4px $gold solid;
  transition: 1s ease;
}
.bussinesItem_content {
  display: grid;
  grid-template: "bisNum1 bisCont2";
  margin-left: 50px;
  grid-template-columns: 50px auto;
}

.bussinesItem_content_num {
  grid-area: bisNum1;
  font-size: 100px;
  color: $gold;
}

.bussinesItem_content_text {
  grid-area: bisCont2;
  font-size: 22px;
  font-style: italic;
}
// Bussines end

//FormQuetion start

.quetionForm {
  margin: 0 auto;
  text-align: center;
  height: 500px;
  display: flex;
  align-items: center;
}
.quetionForm_Img {
  position: relative;
}
.quetionForm_Img img {
  width: 450px;
  height: 450px;
  position: relative;
  z-index: 1;
}
.quetionForm_Img_frame {
  position: absolute;
  width: 425px;
  height: 425px;
  border: 4px $gold solid;
  top: -5px;
  left: 20px;
  transition: 1s ease;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

input:placeholder-shown + label {
  opacity: 0;
  transform: translateY(1rem);
}
.quetionForm_form {
  position: relative;
  z-index: 1;
  width: 400px;
  height: 300px;
  padding: 50px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.quetionForm_title {
  font-size: 45px;
  padding-bottom: 20px;
}
.formInput {
  font-family: Montserrat;
  font-size: 16px;
  font-style: italic;
  letter-spacing: 1px;
  width: 350px;
  border: 0;
  background-color: white;
  border-bottom: 1px $lightGray solid;
  //resize: none;
  overflow: auto;
  box-shadow: 0 0 0 30px white inset !important;
}

//FormQuetion end
